// import axios from "@iso/config/axios-config/axios-instance";
import actions from './actions';
import { rtdb } from "../../library/firebase/firebase";



export const getCustomers = (token) => {
    return dispatch => {
        dispatch(actions.getCustomers());
        let customers = rtdb.ref(`customers/HeadQuarters/`);
        customers.orderByChild('name').on('value', (snapshot) => {
            let data = [];
            snapshot.forEach(child => {
                let x = child.val();
                x = { ...x, name: x.name.toUpperCase() }
                data.push(x);
            })
            dispatch(actions.getCustomersSuccess(data, actions.GET_CUSTOMERS));
        });
    }
}

